import React, { Component, useState } from 'react'
import { Row, Col, Table, ButtonGroup, Button } from 'react-bootstrap';
import Aux from '../../hoc/_Aux';
import temp1Pdf from '../../assets/pdf/liability_waiver.pdf';
import temp2Pdf from '../../assets/pdf/receipt.pdf';
import temp3Pdf from '../../assets/pdf/organize_evidence.pdf';
import { statuses, documents, financials, evidence } from '../../management-data';

import { Document, Page, pdfjs } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const hashDocuments = new Map();
documents.forEach( document_data => hashDocuments.set(document_data.id, document_data.url));
const hashFinancials = new Map();
financials.forEach( financial_data => hashFinancials.set(financial_data.id, financial_data.url));
const hashEvidence = new Map();
evidence.forEach( evidence_data => hashEvidence.set(evidence_data.id, evidence_data.url));

function ExplorerTable(props) {

    const [activeRow, setActiveRow] = useState(0);

    const click_event = (e) => {
        console.log(e.target);
        const parsedID = parseInt(e.target.parentElement.id);
        setActiveRow(parsedID);
        props.activate(parsedID);
    };

    const table_head = (
        <thead className="thead-dark">
            <tr>
                <th>Filename</th>
                <th>Status</th>
                <th>Deadline</th>
                <th>Last Edited</th>
            </tr>
        </thead>
    );

    const data = props.display === 'documents' ? documents :
                    props.display === 'financials' ? financials :
                    props.display === 'evidence' ? evidence : documents;
    const table_rows = data.map( document_data => {
        const { id, name, status, deadline, last_edited } = document_data;
        const status_component = status === statuses.COMPLETE ? <i className="fa fa-check-circle" style={id!==activeRow ? {color: 'green'} : {}}></i> :
                                    status === statuses.INCOMPLETE ? '-' :
                                    status === statuses.OVERDUE ? <i className="fa fa-times-circle"></i> : '-';
        return (
            <tr key={id} id={id} onClick={click_event} style={id===activeRow ? { backgroundColor: '#F76C6C', color: '#ffffff'} : {}}>
                <td>{name}</td>
                <td>{status_component}</td>
                <td>{`${deadline.m}/${deadline.d}/${deadline.y % 100}`}</td>
                <td>{`${last_edited.m}/${last_edited.d}/${last_edited.y % 100}`}</td>
            </tr>
        );
    });

    return (
        <Table size="sm" hover>
            {table_head}
            <tbody>
                {table_rows}
            </tbody>
        </Table>
    );
}



export default class Explorer extends Component {
    
    constructor(props) {
        super(props);

        this.state = {
            pdf_width: 100,
            num_pages: 0,
            curr_page: 1,
            curr_pdf_url: (this.props.display === 'documents' ? temp1Pdf : this.props.display === 'financials' ? temp2Pdf : this.props.display === 'evidence' ? temp3Pdf : temp1Pdf),
        };

        this.onPDFLoad = this.onPDFLoad.bind(this);
        this.advancePDF = this.advancePDF.bind(this);
        this.activate = this.activate.bind(this);
    }

    componentDidMount() {
        // Resize the pdf to the size of the container
        const container = document.getElementById(`pdf-container-${this.props.display}`);
        const col = document.getElementById(`grab-col-${this.props.display}`);
        col.style.minHeight = `${container.clientWidth*1.6}px`
        this.setState({pdf_width: container.clientWidth});
        window.addEventListener('resize', e => {
            this.setState({pdf_width: container.clientWidth});
            col.style.minHeight = `${container.clientWidth*1.6}px`;
        });
    }

    onPDFLoad(pdf) {
        this.setState({num_pages: pdf._pdfInfo.numPages});
    }

    advancePDF(amt) {
        const newPage = Math.min(this.state.num_pages, Math.max(1, this.state.curr_page + amt));
        this.setState({curr_page: newPage});
    }

    activate(id) {
        if (this.props.display === 'documents') {
            this.setState({curr_pdf_url: hashDocuments.get(id), curr_page: 1});
        } else if (this.props.display === 'financials') {
            this.setState({curr_pdf_url: hashFinancials.get(id), curr_page: 1});
        } else if (this.props.display === 'evidence') {
            this.setState({curr_pdf_url: hashEvidence.get(id), curr_page: 1});
        }
    }

    render() {
        return (
            <Aux>
                <Col lg={7} md={8} sm={8} xs={12}>
                    <ExplorerTable activate={this.activate} display={this.props.display} />
                </Col>
                <Col id={`grab-col-${this.props.display}`} className="p-4" style={{backgroundColor: "#eeeeee"}} lg={5} md={4} sm={4} xs={12}>
                    <div className="w-100" id={`pdf-container-${this.props.display}`} style={{boxShadow: "0px 0px 20px #999999"}}>
                        <Document file={this.state.curr_pdf_url} onLoadSuccess={this.onPDFLoad}>
                            <Page pageNumber={this.state.curr_page} width={this.state.pdf_width} />
                        </Document>
                    </div>
                    <Row className="justify-content-center mt-4">
                        <ButtonGroup className="text-center">
                            <Button onClick={e => this.advancePDF(-1)}><i className="fa fa-chevron-left"></i></Button>
                            <Button style={{backgroundColor: '#303d6a'}}>{`${this.state.curr_page}/${this.state.num_pages}`}</Button>
                            <Button onClick={e => this.advancePDF(1)}><i className="fa fa-chevron-right"></i></Button>
                        </ButtonGroup>
                    </Row>
                </Col>
            </Aux>
        );
    }
}
